@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans&family=Shadows+Into+Light+Two&display=swap');

/*--------------------------------------------------------------
#0.1    Theme Reset Style
--------------------------------------------------------------*/
html {
  font-size: 15px;
  scroll-behavior: smooth;
}

body {
  /* font-family: "Heebo", sans-serif; */
  font-family: 'Open Sans', sans-serif;
  background-color: #111;
  font-size: 15px;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
}

@media (max-width: 767px) {
  body {
    font-size: 14px;
  }
}

p {
  color: #999;
  line-height: 1.7em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  /* font-family: "Oswald", sans-serif; */
  font-family: 'Shadows Into Light Two', cursive;
  color: #2a2a2a;
}

ul {
  padding-left: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: #60bca0;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -o-transition: all 0.2s;
  -ms-transition: all 0.2s;
  transition: all 0.2s;
}

a:hover,
a:focus {
  text-decoration: none;
  color: #438B8A;
}

img {
  max-width: 100%;
}

.owl-carousel .owl-item img {
  width: auto;
}

button:focus {
  outline: none;
}

/*--------------------------------------------------------------
#0.2   Global Elements
--------------------------------------------------------------*/
.page-wrapper {
  position: relative;
  overflow: hidden;
}

.border {
  position: absolute;
  left: 50%;
  top: 0;
  width: 1px;
  height: 100%;
  background: rgba(102, 102, 102, .3);
  z-index: -1;
}

.hero-slider-wrapper {
  position: relative;
}

.hero-slider-wrapper:before {
  content: "";
  position: absolute;
  left: 18%;
  top: 0;
  width: 1px;
  height: 100%;
  background: rgba(102, 102, 102, .3);
  z-index: 99;
}

.hero-slider-wrapper:after {
  content: "";
  position: absolute;
  right: 18%;
  top: 0;
  width: 1px;
  height: 100%;
  background: rgba(102, 102, 102, .3);
}

.box-style {
  padding: 0 60px;
}

@media (max-width: 1199px) {
  .box-style {
    padding: 0 35px;
  }
}

@media (max-width: 991px) {
  .box-style {
    padding: 0 20px;
  }
}

@media (max-width: 767px) {
  .box-style {
    padding: 0 10px;
  }
}

.wow {
  visibility: hidden;
}

.fi:before {
  margin: 0;
}

.section-padding {
  padding: 100px 0;
}

@media (max-width: 991px) {
  .section-padding {
    padding: 100px 0;
  }
}

@media (max-width: 767px) {
  .section-padding {
    padding: 90px 0;
  }
}

.mr-bt-0 {
  margin-bottom: 0 !important;
}

.section-title h2 {
  font-size: 48px;
  font-size: 3.2rem;
  font-weight: bold;
  position: relative;
  text-transform: uppercase;
  margin: 0.31em 0 0;
  color: ;
