.portflio .Item {
  transform: matrix(0, 0, 0, 0, 0, 0);
  transition: .4s;
  width: 0;
  display: inline-block;
  opacity: 0;
  height: 0;
  position: relative;
}

.portflio .Item img {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
}

.portflio .Item.Photo,
.portflio .Item.Video,
.portflio .Item.Open {
  transform: matrix(1, 0, 0, 1, 0, 0);
  width: auto;
  width: 25%;
  opacity: 1;
  height: auto;
}

.portflio .Item.Photo img,
.portflio .Item.Video img,
.portflio .Item.Open img {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}


